@mixin pos-center-x($type: absolute) {
    @if ($type == 'absolute' or $type == 'fixed') {
        position: $type;
        left: 50%;
        transform: translateX(-50%);
    }
}

@mixin pos-center-y($type: absolute) {
    @if ($type == 'absolute' or $type == 'fixed') {
        position: $type;
        top: 50%;
        transform: translateY(-50%);
    }
}

@mixin pos-center($type: absolute) {
    @if ($type == 'absolute' or $type == 'fixed') {
        position: $type;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
}
