
                @import "src/scss/_constants.scss";
                @import "src/scss/_mixins.scss";
                
%avatar-base {
    display: block;
    border-radius: 50%;
    overflow: hidden;
    img {
        width: 100%;
        height: 100%;
        display: block;
        object-fit: cover;
    }
}

.avatar24 {
    @extend %avatar-base;
    width: 24px;
    height: 24px;
}

.avatar32 {
    @extend %avatar-base;
    width: 32px;
    height: 32px;
}
